}

.closed-sidebar {
  width: 66px !important;
}

.blink {
  animation: blink-animation .5s steps(5, start) infinite;
  -webkit-animation: blink-animation .5s steps(5, start) infinite;
}

@keyframes blink-animation {
  to {
    visibility: hidden;
  }
}

@-webkit-keyframes blink-animation {
  to {
    visibility: hidden;
  }
}

.waitingForConnection {
  animation: blinker .5s cubic-bezier(.5, 0, 1, 1) infinite alternate;
}

@keyframes blinker {
  to {
    opacity: 0;
  }
}

.waitingForConnection2 {
  animation: blinker2 .5s cubic-bezier(.5, 0, 1, 1) infinite alternate;
}

@keyframes blinker2 {
  to {
    opacity: 0;
  }
}

.waitingForConnection3 {
  animation: blinker3 .5s cubic-bezier(.5, 0, 1, 1) infinite alternate;
}

@keyframes blinker3 {
  to {
    opacity: 0;
  }
}

*::-webkit-scrollbar,
*::-webkit-scrollbar-thumb {
  width: 26px;
  border-radius: 13px;
  background-clip: padding-box;
  border: 10px solid transparent;
}

*::-webkit-scrollbar-thumb {
  box-shadow: inset 0 0 0 10px;
}

@media (max-width:1199px) {

  .rightCntlogin {
    padding: 50px;
  }
}

@media (max-width:767px) {

  .accountOverlay {
    background-color: rgba(0, 0, 0, .3);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
  }
}



