 /* Table   */

table {
  font-family: arial, sans-serif;
  border-collapse: collapse;
  width: 100%;
}

td, th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
}

tr:nth-child(even) {
  background-color: #dddddd;
}

.eyecon{
  margin: auto;
}


.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* height: 100vh; */
  background: darkgray;
    border: 1px solid;
    height: 400px;
    width: 400px;
    /* margin: 17px px; */
    border-radius: 9px;
    margin-top: 32px;
}

.text-field {
  padding: 7px;
  margin: 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 100%;
